:root {
  --first-text-color: #fcfcfc;
  --second-text-color: #131313;
  --akcent-color: #f8d24e;
  --hover-color: #4d5ae5;
  --main-background-color: #84a178;
  --animatin-time: 250ms;
}

*, :before, :after {
  box-sizing: border-box;
}

a {
  color: inherit;
  text-decoration: none;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

button {
  cursor: pointer;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  background-color: var(--main-background-color);
  font-family: Montserrat, sans-serif;
}

.container {
  min-width: 320px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.header {
  outline: 1px solid #00f;
  position: relative;
}

.header .container {
  display: flex;
}

.logo-icon {
  width: 40px;
  height: 37px;
}

.header-menu-icon {
  width: 32px;
  height: 32px;
}

.header-menu {
  display: none;
}

.header-menu.is-open {
  width: 100vw;
  outline: 1px solid red;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 40px;
  display: flex;
  position: absolute;
  top: 100%;
  left: 0;
}

.logo {
  margin-right: auto;
}

.header-menu-button {
  background-color: var(--main-background-color);
  border: none;
}

.icon-cross {
  display: none;
}

.header-menu-button.is-open .icon-cross {
  display: block;
}

.header-menu-button.is-open .icon-burger {
  display: none;
}

.navigation-link {
  letter-spacing: 0;
  color: var(--first-text-color);
  font-size: 32px;
  font-weight: 700;
  line-height: 1.22;
}

.navigation-list > li:not(:last-child) {
  margin-bottom: 32px;
}

.navigation {
  margin-bottom: 114px;
}

.navigation-list > li {
  text-align: center;
}

.navigation-link:hover {
  color: var(--second-text-color);
}

.navigation-button {
  background-color: var(--akcent-color);
  letter-spacing: 0;
  text-align: center;
  border: none;
  border-radius: 10px;
  padding: 12px 36px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.21;
}

.navigation-button:after {
  content: "";
  width: 12px;
  height: 12px;
  background-image: url("arrow.7a88aef5.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-origin: content-box;
  border-color: rgba(0, 0, 0, 0);
  margin-left: 4px;
  display: inline-block;
}

